import React from 'react';
import { NavLink } from 'react-router-dom';
import CopyrightContainer from './CopyrightContainer';
import Socials from './Socials';
// eslint-disable-next-line
import styled from 'styled-components';
import './Overlay.css';

export default function Overlay({ active, setActive }) {
  const linksInfo = [
    {
      extension: '/',
      title: 'Home'
    },
    {
      extension: '/Development',
      title: 'Development'
    },
    {
      extension: '/Design',
      title: 'Design'
    },
    {
      extension: '/Skills',
      title: 'Skills'
    }
  ];
  const links = linksInfo.map(({ extension, title }, id) => (
    <li key={id}>
      <NavLink
        exact
        activeClassName="active"
        to={`${extension}`.toLowerCase()}
        onClick={() => setActive(!active)}
      >
        {title}
      </NavLink>
    </li>
  ));
  return (
    <div className={`overlay ${active ? 'open' : ''}`}>
      <div className="overlay-wrapper">
        <ul>{links}</ul>
        <CopyrightContainer />
        <div className="social-icons">
          <Socials />
        </div>
      </div>
    </div>
  );
}
